.page-footer {
  z-index: 2;
  padding: 35px 0 30px;
  font-family: $ttnorms;
  background: $navy;
  color: rgba($white, .4);

  @include media-breakpoint-up(md) {
    padding: 55px 0 60px;
  }

  a {
    color: rgba($white, .4);
    text-decoration: none;
    transition: .2s color ease-in-out;

    &:hover {
      color: $white;
    }
  }

  p {
    color: rgba($white, .4);
    font-size: 18px;
    line-height: 1.5;
    letter-spacing: 0.04em;
    font-weight: 500;
  }

  ul {
    list-style: none;
  }

  .copyright, .logo, .socials {
    clear: right;

    @include media-breakpoint-up(lg) {
      float: left;
      clear: left;
    }
  }

  .footer {
    &-menu {
      display: flex;
      flex-direction: column;
      margin: 0;
      padding: 0;

      &-column {
        float: left;
        width: 100%;
        margin: 30px 0 0;

        @include media-breakpoint-up(sm) {
          width: 50%;
          float: right;
        }

        @include media-breakpoint-up(lg) {
          margin: 0;
          width: 30%;
        }

        @include media-breakpoint-up(xl) {
          width: 20%;
        }

        &-title {
          font-size: 14px;
          line-height: 1.2;
          font-weight: 500;
          margin: 0 0 12px 0;
          color: $white;
          text-transform: uppercase;

          @include media-breakpoint-up(sm) {
            margin: 0 0 26px 0;
          }
        }
      }

      li + li {
        margin-top: 8px;

        @include media-breakpoint-up(sm) {
          margin-top: 12px;
        }
      }

      a {
        font-size: 14px;
        line-height: 1.2;
      }
    }
  }

  .socials {
    margin: 15px 0 0;

    @include media-breakpoint-up(md) {
      margin-top: 25px;
      margin-bottom: 0;
    }

    a {
      padding: 0;
    }

    li + li {
      margin-left: 14px;
    }
  }

  .copyright {
    font-size: 14px;
    line-height: 2;
    font-weight: 300;
    margin: 0;
    width: 100%;
    clear: both;
    padding-right: 30px;
    padding-top: 30px;

    @include media-breakpoint-up(lg) {
      padding-top: 12px;
      width: 40%;
      clear: left;
    }

    @include media-breakpoint-up(xl) {
      width: 60%;
    }
  }
}
